import React from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom"

import Home from "./pages/Home"
import Contact from "./pages/Contact"
import Knowledges from "./pages/Knowledges"
import Portfolio from "./pages/Portfolio"
import NotFound from "./pages/NotFound"
import Formations from './pages/Formations';


const App = () => {
  return (
    <>
      <BrowserRouter>
        <Switch>
          <Route path="/" exact component={Home}/>
          <Route path="/website" exact component={() => <Home isFirst={true} />}/>
          <Route path="/website/formations" exact component={Formations}/>
          <Route path="/website/competences" exact component={Knowledges}/>
          <Route path="/website/portfolio" component={Portfolio}/>
          <Route path="/website/contact" component={Contact}/>
          <Route component={NotFound}/>
        </Switch>
      </BrowserRouter>
    </>
  );
};

export default App;