import React, { Component } from 'react';

class Project extends Component {

    state = {
        showInfo: false,
    }

    handleInfo = () => {
        this.setState({
            showInfo:!this.state.showInfo
        })
    }

    render() {
        let { name, other, languagesIcons, source, info, picture } = this.props.item;
        
        return (
            <div className='project'>
                <div className='icons'>
                    {languagesIcons.map( icon => 
                        <i className={icon} key={icon}></i>)}
                    {other && <i className="other"> <img src={other} alt=""/></i>}
                </div>
                <h3 class='project-title'>{name}</h3>
                <img className="pict" src={picture} alt="" onClick={this.handleInfo}/>
                <span className="infos" onClick={this.handleInfo}>
                    <i className="fas fa-plus-circle"></i>
                </span>

                {
                    this.state.showInfo && (
                        <div className="showInfos">
                            <div className="infosContent">
                                
                                <div className="head">
                                <h2>{name}</h2>
                                { source != null &&
                                <div className="sourceCode" >
                                    <a href={source} rel="noopener noreferrer" className="button" target="_blank">Code source</a>
                                </div>
                                }
                                </div>
                                
                                <p className="text" dangerouslySetInnerHTML={{__html: info}}></p>

                                <div className="button return" onClick={this.handleInfo}>Retourner sur la page</div>
                            </div>
                        </div>
                    )
                }

            </div>
            
        );
    }
}
export default Project;
